/* This pen */

 body {
	 font-size: 16px;
	 text-rendering: optimizeLegibility;
	 font-weight: normal;
	 font-weight: initial;
} 
 .dark {
	 background: #110f16;
}
 .light {
	 background: white;
}
 .categoriesHomepage_title, .categoriesHomepage_title:hover {
	 text-decoration: none;
	 transition: color 0.3s ease-in-out;
} 
 #pageHeaderTitle {
	 margin: 2rem 0;
	 text-transform: uppercase;
	 text-align: center;
